import React, { memo } from 'react';
import styled from 'styled-components';
import { colors, viewports } from '../../utils/variables';
import { Label } from '../styled/Typography';
import Input from '../styled/Input';
import ErrorMessage from './ErrorMessage';

const PasswordInput = ({
  label,
  name,
  placeholder,
  forgotLink,
  value,
  onChange,
  error,
  touched,
  height,
  onForgotClick,
}) => (
  <Base>
    {label && <Label htmlFor={name}>{label}</Label>}
    {forgotLink && (
      <ForgotPassword onClick={onForgotClick}>Forgot Password?</ForgotPassword>
    )}
    <Input
      type="password"
      name={name}
      placeholder={placeholder}
      value={value}
      onChange={onChange}
      error={touched && error}
    />
    <ErrorMessage visible={touched && error} error={error} height={height} />
  </Base>
);

export default memo(PasswordInput);

const Base = styled.div`
  margin-bottom: 1.875rem;
  position: relative;
`;

const ForgotPassword = styled.span`
  color: ${colors.blue};
  cursor: pointer;
  font-size: 0.75rem;
  position: absolute;
  right: 0;
  top: 0;

  @media ${viewports.medium} {
    &:hover {
      color: ${colors.hoverBlue};
    }
  }
`;
