import React, { memo } from 'react';
import Helmet from 'react-helmet';
import { StaticQuery, graphql, navigate } from 'gatsby';
import GlobalStyle from '../styled/GlobalStyle';
import favicon from '../../static/icons/favicon.png';
import appleTouchIcon from '../../static/icons/apple-touch-icon.png';
import ErrorHandler from './ErrorHandler';
import { authFromLocalCookie } from '../../utils/auth';

const Layout = ({ children, title, redirect, protectedRoute }) => {
  const authenticated = authFromLocalCookie();
  if (process.browser) {
    if (protectedRoute && !authenticated) navigate(redirect);
    if (!protectedRoute && authenticated) navigate(redirect);
  }

  if (protectedRoute && !authenticated) return null;
  if (!protectedRoute && authenticated) return null;

  return (
    <StaticQuery query={SiteQuery}>
      {({ site }) => (
        <>
          <Helmet title={title || site.siteMetadata.title}>
            <html lang="en" />
            <meta charSet="UTF-8" />
            <meta
              name="viewport"
              content="width=device-width, initial-scale=1.0"
            />
            <link
              rel="stylesheet"
              type="text/css"
              href="https://fast.fonts.net/cssapi/9018b580-e0c2-499e-a8d4-78eff0bdafd9.css"
            />
            <link rel="icon" href={favicon} />
            <link
              rel="apple-touch-icon"
              sizes="180x180"
              href={appleTouchIcon}
            />
          </Helmet>
          <GlobalStyle />
          <ErrorHandler>{children}</ErrorHandler>
        </>
      )}
    </StaticQuery>
  );
};

export default memo(Layout);

const SiteQuery = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`;
