import React, { memo, useState } from 'react';
import queryString from 'querystring';
import styled from 'styled-components';
import gql from 'graphql-tag';
import { Formik, Form, Field } from 'formik';
import { Mutation } from 'react-apollo';
import Layout from '../components/shared/Layout';
import PasswordInput from '../components/inputs/PasswordInput';
import SubmitButton from '../components/shared/SubmitButton';
import { resetPasswordValidation } from '../utils/validationSchema';
import { MapBackground } from '../components/styled/Background';
import { viewports } from '../utils/variables';
import { Heading, Subheading } from '../components/styled/Typography';
import ServerErrorMessage from '../components/inputs/ServerErrorMessage';
import Success from '../components/reset-password/Success';

const ResetPasswordPage = ({ location }) => {
  const [reset, setReset] = useState(false);
  return (
    <Layout title="ArtCenter - Reset Password">
      <Base>
        <MapBackground />
        <Container>
          {!reset ? (
            <Mutation mutation={RESET_PASSWORD}>
              {(resetPassword, { error }) => (
                <Formik
                  initialValues={{
                    newPassword: '',
                    confirmNewPassword: '',
                  }}
                  validate={resetPasswordValidation}
                  onSubmit={async (values, actions) => {
                    const { search } = location;
                    const { token } = queryString.parse(
                      search.slice(1, search.length)
                    );
                    try {
                      await resetPassword({
                        variables: {
                          token,
                          password: values.newPassword,
                        },
                      });
                      actions.setSubmitting(false);
                      setReset(true);
                    } catch (err) {
                      actions.setSubmitting(false);
                    }
                  }}
                  validateOnChange={false}
                  validateOnBlur={false}
                >
                  {({ isSubmitting, errors, touched }) => (
                    <Form>
                      <Heading>Reset Password</Heading>
                      <Subheading>Please reset your password</Subheading>
                      {!isSubmitting && (
                        <ServerErrorMessage visible={error} error={error} />
                      )}
                      <Field name="newPassword">
                        {({ field }) => (
                          <PasswordInput
                            {...field}
                            label="Password"
                            placeholder="Enter new password"
                            error={errors.newPassword}
                            touched={touched.newPassword}
                          />
                        )}
                      </Field>
                      <Field name="confirmNewPassword">
                        {({ field }) => (
                          <PasswordInput
                            {...field}
                            label="Confirm password"
                            placeholder="Enter password again"
                            error={errors.confirmNewPassword}
                            touched={touched.confirmNewPassword}
                            height={36}
                          />
                        )}
                      </Field>
                      <SubmitButton
                        disabled={isSubmitting}
                        submitting={isSubmitting}
                      >
                        Submit
                      </SubmitButton>
                    </Form>
                  )}
                </Formik>
              )}
            </Mutation>
          ) : (
            <Success />
          )}
        </Container>
      </Base>
    </Layout>
  );
};

export default memo(ResetPasswordPage);

const Base = styled.div`
  align-items: center;
  display: flex;
  height: 100vh;
  justify-content: center;
  margin: 0 auto;
  width: 100%;
`;

const Container = styled.div`
  background-color: #fff;
  max-width: 30rem;
  box-shadow: 8px 8px 20px 0 rgba(34, 34, 34, 0.06);
  padding: 2.75rem 1.875rem;
  position: relative;
  width: 100%;

  @media ${viewports.medium} {
    padding: 3.4375rem 3.75rem;
  }
`;

const RESET_PASSWORD = gql`
  mutation resetPassword($password: String!, $token: String!) {
    resetPassword(password: $password, token: $token) {
      id
      email
    }
  }
`;
